import "core-js/modules/es.array.filter.js";
import "core-js/modules/es.object.to-string.js";
import { Delete, Calendar } from '@element-plus/icons-vue';
import { reactive, ref, watch } from 'vue';
import { useStore } from 'vuex';
import { setTime } from '../../views/Todos/time.js'; // import { setTodos } from '../../utils/Todos.js'

import emitter from '../../utils/eventbus.js';
export default {
  components: {
    Delete: Delete,
    Calendar: Calendar
  },
  props: ['newGetTodo', 'ifShow', 'active'],
  setup: function setup() {
    // 拿到仓库
    var store = useStore(); // mapState接收todos的数据

    var getTodo = ref([]);
    var todoThings = reactive({
      todos: '',
      time: setTime(new Date()),
      newTodos: []
    }); // 删除todos

    var deleteTodo = function deleteTodo(id, newGetTodo) {
      todoThings.newTodos = [];
      todoThings.newTodos = newGetTodo.filter(function (todo) {
        return todo.id !== id;
      });
      store.dispatch('deleteTodo', todoThings.newTodos);
    }; // 改变事件的选中状态


    var changeStatusOfThing = function changeStatusOfThing(status, todos, newGetTodo) {
      todos.isChecked = !status; // 通知本地存储改变选中状态再次渲染页面

      store.dispatch('deleteTodo', newGetTodo);
      getTodo.value = newGetTodo;
    };

    watch(getTodo.value, function () {
      emitter.emit('updataPage');
    });
    watch(function () {
      return todoThings.newTodos;
    }, function () {
      emitter.emit('updataPage');
    });
    return {
      getTodo: getTodo,
      todoThings: todoThings,
      deleteTodo: deleteTodo,
      changeStatusOfThing: changeStatusOfThing
    };
  }
};