import "core-js/modules/es.array.filter.js";
import "core-js/modules/es.object.to-string.js";
import { reactive, ref } from 'vue'; // vuex仓库

import { useStore } from 'vuex'; // 时间展示函数

import { setTime } from './time.js'; // 本地存储的获取函数

import { getTodos, getEventPoint } from '../../utils/Todos.js';
import { nanoid } from 'nanoid';
import TodosListData from '../../components/todosListData/index.vue'; // 引入全局事件总线

import emitter from '../../utils/eventbus.js';
export default {
  name: 'Todos',
  components: {
    TodosListData: TodosListData
  },
  setup: function setup() {
    // 拿到仓库
    var store = useStore();
    var todoThings = reactive({
      todos: '',
      time: setTime(new Date()),
      newTodos: [],
      isDisable: true
    }); // mapState接收todos的数据

    var getTodo = ref(getTodos());
    var ifShow = ref(false); // 点击整理todos数据，将todos事件提交给仓库

    var showing = function showing() {
      var res = ref(getEventPoint());
      todoThings.time = setTime(new Date());
      getTodo.value = '';
      store.dispatch('todoKeep', {
        todos: todoThings.todos,
        time: todoThings.time,
        eventPoint: res.value[0] ? res.value[0].eventPoint + 1 : 1,
        id: nanoid(),
        isChecked: false
      });
      todoThings.todos = '';
      getTodo.value = getTodos();
    }; // 批量删除


    var batchDel = function batchDel(todos) {
      todoThings.newTodos = [];
      todoThings.newTodos = todos.filter(function (todo) {
        return todo.isChecked !== true;
      });
      getTodo.value = '';
      store.dispatch('deleteTodo', todoThings.newTodos);
      getTodo.value = getTodos();
    }; // 显示批量操作checkbox


    emitter.on('openCheckbox', function (value) {
      ifShow.value = value;
    });
    emitter.on('updataPage', function () {
      getTodo.value = getTodos();
    });
    return {
      todoThings: todoThings,
      getTodo: getTodo,
      ifShow: ifShow,
      showing: showing,
      batchDel: batchDel
    };
  }
};